import React from 'react';
import Form from 'react-bootstrap/Form';
import Dropdown from 'react-bootstrap/Dropdown';
import { Link } from 'react-router-dom';
import './SortSelect.css';
import { getSortText, createDate, updateDate, likes, views, desc ,asc } from '../../modules/sortingHelper';
const CommentSelect = ({ onChange, sort, dir}) => (
    <Form.Select size='sm' onChange = {onChange} defaultValue = {sort + "#" + dir}>
            <option value = {updateDate + "#" +  desc}>
                    ÏµúÍ∑ºÏàú
            </option>
            <option value = {updateDate + "#" +  asc}>
                    Ïò§ÎûòÎêúÏàú
            </option>
    </Form.Select>)


const ItemSelect = ({ onChange, sort ,dir}) => (
    <Form.Select size='sm' onChange = {onChange} defaultValue = {sort + "#" + dir}>
            <option value = {createDate + "#" +  desc}>
                    ÏµúÍ∑ºÏàú
            </option>
            <option value = {createDate + "#" +  asc}>
                    Ïò§ÎûòÎêúÏàú
            </option>
            <option value = {likes + "#" +  desc}>  
                    Ï¢ãÏïÑÏöî ÎßéÏùÄ Ïàú
            </option>
            <option value = {likes + "#" +  asc}>
                    Ï¢ãÏïÑÏöî Ï†ÅÏùÄ Ïàú
            </option>
            <option value = {views + "#" +  desc}>
                    Ï°∞ÌöåÏàò ÎßéÏùÄ Ïàú
            </option>
            <option value = {views + "#" +  asc}>
                    Ï°∞ÌöåÏàò Ï†ÅÏùÄ Ïàú
            </option>
    </Form.Select>)

const ItemDropdown = ({ type, sort ,dir }) => {

        const getUrl = (s, d) => ("/" + type + "/list?page=1&size=16&sort=" + s + "&dir=" + d);

        return (
                <Dropdown>
                        <Dropdown.Toggle variant="primary" id="dropdown-basic">
                        {getSortText(sort, dir)}
                        </Dropdown.Toggle>

                <Dropdown.Menu>
                        <Dropdown.Item className = "sort-item">
                                <Link className = "sort-link" to = {getUrl(createDate, desc)}>
                                ÏµúÍ∑ºÏàú
                                </Link>
                        </Dropdown.Item>
                        <Dropdown.Item className = "sort-item">
                                <Link className = "sort-link" to = {getUrl(createDate, asc)}>
                                Ïò§ÎûòÎêúÏàú
                                </Link>
                        </Dropdown.Item>
                        <Dropdown.Item className = "sort-item">
                                <Link className = "sort-link" to = {getUrl(likes, desc)}>
                                Ï¢ãÏïÑÏöî ÎßéÏùÄ Ïàú
                                </Link>
                        </Dropdown.Item>
                        <Dropdown.Item className = "sort-item">
                                <Link className = "sort-link" to = {getUrl(likes, asc)}>
                                Ï¢ãÏïÑÏöî Ï†ÅÏùÄ Ïàú
                                </Link>
                        </Dropdown.Item>
                        <Dropdown.Item className = "sort-item">
                                <Link className = "sort-link" to = {getUrl(views, desc)}>
                                Ï°∞ÌöåÏàò ÎßéÏùÄ Ïàú
                                </Link>
                        </Dropdown.Item>
                        <Dropdown.Item className = "sort-item">
                                <Link className = "sort-link" to = {getUrl(views, asc)}>
                                Ï°∞ÌöåÏàò Ï†ÅÏùÄ Ïàú
                                </Link>
                        </Dropdown.Item>
                
                </Dropdown.Menu>
                </Dropdown>
        )
}

const SortSelect = ({type, sort , dir, handleChange}) => {

    // handleChange Ïóê ÏÑ†ÌÉùÎêú sort, dir Ï†ÑÎã¨
    const onChange = (e) => {
            const [ extSort, extDir ] = e.target.value.split("#");
            handleChange({
                sort : extSort,
                dir : extDir,
            });
    }  
    return (
            <>
            {type === 'comment' ?
                    <CommentSelect
                        onChange = {onChange}
                        sort = {sort}
                        dir = {dir}
                    /> :
                    <ItemDropdown 
                        type = {type}
                        sort = {sort}
                        dir = {dir}
                    />}
            </>
    )
}

export default SortSelect;